body {
  margin: 0 !important;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif, "Lexend Deca";
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.nav-link{
  text-decoration: none;
  width: 100%;
}

.content {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: white;
  grid-gap: 50px;
  gap: 50px;
  height: 100%;
  width: 100%;
  margin: 0px auto;
}
.App {
  height: 100%;
  width: 100%;
  position: absolute;
  justify-content: center;
  align-items: center;
}
.topButtons{
  display: flex;
  justify-content: space-between;  
  position: absolute;
  top: 0;
  left: 0;
  width: 90%;
  z-index:99;
  padding: 5%;
}

.fullScreenBtn{
  margin-left: auto;
}

#roundBtn {
  display: flex;
  background-color: #6FAFC9;
  border: none;
  color: white;
  padding: 15px;
  font-size: 30px;
  border-radius: 50%;
  height: 100%;
}

.svg-inline--fa {
  width: 1em;
}
.textButton {
  width: 100%;
  align-items: center;
  justify-content: center;
  display: flex;
  width: 100%;
}

#textBtn {
  border: none;
  color: #121212;
  padding: 15px;
  font-size: 22px;
  border-radius: 35px;
  width: 80%;
  background-color: #E2E2E2;
  font-family: "Lexend Deca";
}
.Home {
  background-color: white;
  height: 100%;
  width: 100%;
  justify-content: center;
  align-items: center;
}

.menu{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: white;
  grid-gap: 10px;
  gap: 10px;
  width: 100%;
}

.logo{
  max-height: 20rem;
  max-width: 60rem;
}
.RealidadeVirtual {
  background-color: white;
  height: 100%;
  width: 100%;
  justify-content: center;
  align-items: center;
}
.RealidadeAumentada {
    height: 100%;
    width: 100%;
    justify-content: center;
    align-items: center;
}
